@import "../global.scss";

*{
    color: white;
}


main {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    position:relative;
    top: 135px;
    z-index: 10;
    

    #introHome, #infoHome{
        background-color:rgba(95, 158, 160, 0.85);
        background-color:rgba(255, 255, 255, 0.8);
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding: 0px 0px ; 
        margin: 0 auto 50px;
        border: solid 3px white;
        height: 300px;
        >*{
            //border: solid;
            width: 30%;
            height: auto;

        }
        h1{
            font-family: $primary-font-medium;
            font-size: 3rem;
            color: black;
            height: fit-content;
            text-align: start;
            margin-left: 20px;
        }
        .motto {
            display: inline-block;
            font-family: $primary-font-medium;
            font-size: 2vw;
            background-color: none;
            //clip-path: polygon(2% 0, 100% 6%, 99% 97%, 0% 100%);
            //padding: 50px;
            margin-right: 20px;
            width: 30%;
            height:fit-content;
            color: black;
            //border: groove white 10px;
        }
        #JRHome, #JRInfoHome {
            font-size: 10vw;
            font-family:typekeys;
            color: black;
            vertical-align:baseline;
            line-height: 300px;
            text-align: center;
            padding-top: 50px;
            //border:solid;
        }
    }
}

section{
    color:antiquewhite;
    //background-color: aqua;
    border: solid 3px white;
    width: calc(50% - 6px);
    height: 500px;
    display: inline-block;
    border-color: solid 2px white;
    background-image: url(../Images/buchMitLicht.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: bottom;
    h2 {
        padding-left: 20px;
    }
    ul, li {
        padding: 5px 20px;
        border: solid 1px white;
        margin-bottom: 10px;
        width: fit-content;
        font-weight: 900;
        background-color:rgba(48, 46, 46, 0.5);
        list-style: none;
        line-height: 20px;
        
    }
    a{
        text-decoration: none;
    }
}

#homeTexte1{
    overflow: hidden;
    ul{
        width: 50%;
    }
    p{
        position: relative;
        right: -60%;
        top: -190px;
        width: 50%;
        height: 100%;
        padding: 15px;
        line-height: 2rem;
        opacity: 0.5;
        transition: 1s ease-in;
        overflow: hidden;
        margin-bottom: 10px;
    }
    p:hover {
        position: relative;
        right: -47%;
        top: -170px;
        //width: 50%;
        //height: calc(fit-content + 10%);
        //padding: 15px;
        //padding-bottom: 130px;
        line-height: 2rem;
        opacity: 1;
        //background: rgba(95, 158, 160);
        background: white;
        color: black;
        overflow-y: scroll;
        transition:  1s ease-in;
        margin-bottom: 10px;
    }

}

#homeTexte2{
    background-image: url(../Images/homepagePLDM.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: bottom;
    background-blend-mode:darken;
    background-color: rgba(0, 0, 0, 0.5);
    
    h2{
        //color: black;
    }
}
#homeTexte2:hover{
    background-color: transparent;
    background-blend-mode:0.0;
    h2{
        background-color: rgba(48, 46, 46, 0.5);
        
    }
}

.info{
